import "./styles/index.scss";
import "bootstrap";
import Parallax from "parallax-js";

//------------Mobile menu------------------------

//Menu tabs
const mobileMenuLinks = document.querySelectorAll(".menu-item");
mobileMenuLinks.forEach((link) => {
  link.addEventListener("click", toggleNestedMenu);
});

function toggleNestedMenu(event) {
  event.stopPropagation();
  const nestedMenu = event.target
    .closest(".menu-item")
    .querySelector("ul.sub-menu");

  if (nestedMenu) {
    event.target.closest(".menu-item").classList.toggle("active");
    nestedMenu.classList.toggle("active");
  }
}

//Burger toggle
const overlay = document.getElementById("overlay");
const burgerButton = document.querySelector(".navbar__toggle");
const burgerMenu = document.getElementById("nav-mobile");
const closeMobMenuButton = document.querySelector(".nav-mobile__close");

burgerButton.addEventListener("click", showMobMenu);
closeMobMenuButton.addEventListener("click", closeMobMenu);

function showMobMenu() {
  burgerMenu.classList.add("active");
  overlay.classList.add("active");
  document.body.classList.add("noscroll");
}

function closeMobMenu() {
  burgerMenu.classList.remove("active");
  overlay.classList.remove("active");
  document.body.classList.remove("noscroll");
}

