//ФОРМА ЗАЯВКИ МОДАЛКА

const modalWindow = document.querySelector(".modal-apply");
const modalWindowCloseBtn = document.querySelector(".modal-close");
const benefitsButtonModal = document.querySelector(".benefits__btn-order");
const headerButtonModal = document.querySelector(".header-apply");
const mobileHeaderButtonModal = document.querySelector(".nav-header-apply");
const qualityButtonModal = document.querySelector(".quality__left-button");
const noSubmenuMobileLinks = document
  .querySelector(".nav-mobile__menu")
  .querySelectorAll(".no-submenu");

benefitsButtonModal.addEventListener("click", showModal);
qualityButtonModal.addEventListener("click", showModal);
headerButtonModal.addEventListener("click", showModal);
mobileHeaderButtonModal.addEventListener("click", showModalMobile);
noSubmenuMobileLinks.forEach((link) =>
  link.addEventListener("click", function () {
    burgerMenu.classList.remove("active");
    overlay.classList.remove("active");
    document.body.classList.remove("noscroll");
  })
);

modalWindowCloseBtn.addEventListener("click", hideModal);
overlay.addEventListener("click", hideModal);

function showModal() {
  modalWindow.classList.add("active");
  overlay.classList.add("active");
  document.body.classList.add("noscroll");
}

function hideModal() {
  modalWindow.classList.remove("active");
  overlay.classList.remove("active");
  document.body.classList.remove("noscroll");
}

function showModalMobile() {
  burgerMenu.classList.remove("active");
  modalWindow.classList.add("active");
  overlay.classList.add("active");
  document.body.classList.add("noscroll");
}

//FORM VALIDATE
$("#apply").validate({
  rules: {
    name: {
      required: true,
    },
    phone: {
      required: true,
    },
    address: {
      required: true,
    },
    quantity: {
      required: true,
    },
  },
  messages: {
    name: {
      required: "Введите Ваше имя",
    },
    phone: {
      required: "Введите номер телефона",
    },
    address: {
      required: "Введите адрес доставки",
    },
    quantity: {
      required: "Введите количество",
    },
  },
  submitHandler: function (form) {
    ajaxFormSubmit();
    form.reset();
  },
});

// Функция AJAX запрса на сервер

function ajaxFormSubmit() {
  let string = $("#apply").serialize(); // Соханяем данные введенные в форму в строку.
  console.log(string);

  //Формируем ajax запрос
  $.ajax({
    type: "POST", // Тип запроса - POST
    url: "src/php/mail.php", // Куда отправляем запрос
    data: string, // Какие даные отправляем, в данном случае отправляем переменную string

    
  });
  // Чтобы по Submit больше ничего не выполнялось - делаем возврат false чтобы прервать цепчку срабатывания остальных функций
  return false;
}
